.contactWrapper {
    background-color: #99d0fb;
    min-height: max-content;
    padding:50px 0 50px 0;
}

.container {
    height: 100%;
    width: 80%;
    margin: 0 auto;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

}

.title {
    margin-top: 0;
    width: max-content;
    background-color: grey;
    padding: 5px;
}

.containerForm {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

input {
    margin-bottom: 20px;
    width: 250px;
}

textarea {
    height: 100px;
    width: 250px;
    margin-bottom: 50px;
    resize: none;
}

.button {
    width: 150px;
    height: 30px;
    text-decoration: none;
    padding-top: 9px;
    color: #a675b3;
    text-align: center;
    line-height: 20px;
    display: block;
    font: normal 17px arial;
}

.button:not(.active) {
    box-shadow: inset 0 1px 1px rgba(111, 55, 125, 0.8), inset 0 -1px 0px rgba(63, 59, 113, 0.2), 0 9px 16px 0 rgba(0, 0, 0, 0.3), 0 4px 3px 0 rgba(0, 0, 0, 0.3), 0 0 0 1px #150a1e;
    background-image: linear-gradient(#3b2751, #271739);
    text-shadow: 0 0 21px rgba(223, 206, 228, 0.5), 0 -1px 0 #311d47;
}

.button:not(.active):hover,
.button:not(.active):focus {
    transition: color 200ms linear, text-shadow 500ms linear;
    color: #fff;
    text-shadow: 0 0 21px rgba(223, 206, 228, 0.5), 0 0 10px rgba(223, 206, 228, 0.4), 0 0 2px #2a153c;
}
.button:not(:hover) {
    transition: 0.6s;
}
@media screen and (max-height: 820px) {
    .contactWrapper{
        padding: 20px 0 20px 0;
    }
}