.footerWrapper{
    height: 200px;
    background-color: #76eeae;
    padding-top: 20px;
}

.container{
    border: 1px solid;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
}
.title{
    background-color: grey;
    max-width: max-content;
    margin: 0;
}
.items{
    border: 1px solid;
    display: flex;
     width: 80%;
    justify-content: space-between;
}
.item{
    width: 50px;
    height: 50px;
    background-color: grey;
}
